.PageLogin {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
}
.CardLogin {
  width: 30em;
  height: 30em;
  max-width: 80%;
  background-color: white;
  padding: 2.4em 3em;
  border: solid 2.6px black;
  border-radius: 2em;
  color: black;
}
.LoginTitulo {
  width: 100%;
  height: 25%;
}
.LoginForm {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 75%;
}
.LoginForm label {
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 1.2em;
}
.LoginForm input {
  width: 240px;
  height: 40px;
  border: solid 2.6px black;
  border-radius: 0.36em;
  padding: 0em 0.6em;
  font-size: 16px;
  font-weight: 500;
  color: black;
}
.LoginForm:focus, .LoginForm input {
  box-shadow: 0 0 0 0;
  outline: none;
}
.LoginForm input.erro {
  border: solid 2.5px red;
  animation: erro 400ms linear; 
}
.BtnLogar {
  margin-top: 1.2em;
  width: 7.8em;
  height: 3em;
  background-color: #4169E1;
  border-radius: 0.36em;
  border: none;
}

.BtnLogar p {
  font-size: 1.28em;
  font-weight: 600;
  color: white;
}
.BtnLogar:active {
  transition: transform 300ms ease;
  transform: scale(1.2);
}

.Email-Incorreto {
  display: none;
  position: absolute;
  right: 0.5em;
  color: red;
}
.Email-Incorreto.erro {
  display: block;
}

.No-User {
  display: none;
  position: absolute;
  right: 0.5em;
  color: red; 
}
.No-User.erro {
  display: block;
}
.Senha-Incorreta {
  display: none;
  position: absolute;
  right: 0.5em;
  color: red;
}
.Senha-Incorreta.erro {
  display: block;
}

@keyframes erro {
  0% {
    transform: translatex(0);
  }
  25% {
    transform: translatex(-0.6em);
  }
  50% {
    transform: translatex(1.2em);
  }
  75% {
    transform: translatex(-1.2em);
  }
  100% {
    transform: translatex(0);
  }
}

@media (min-width: 960px) {
  .CardLogin {
   transform: scale(1.3);
}
}
@media (min-width: 1280px) {
  .CardLogin {
    transform: scale(1.6);
  }
}